<template>
  <div>
    <div class="btns">
      <div class="photo" @click="photoHandle">打开摄像头</div>
      <div class="snap" @click="snapHandle">截取图像</div>
      <div class="close" @click="closeHandle">关闭摄像头</div>
    </div>

    <div class="photo">
      <canvas width="200" height="150"></canvas>
      <video width="200" height="150"></video>
    </div>
    <el-foot index="0"></el-foot>
  </div>
</template>

<script>
  import elFoot from './common/foot'
export default {
  name: 'camera',
  components: {
    elFoot
  },
  data () {
    return {
      msg: '',
      context: null,
      video: null,
      mediaStreamTrack: null,
      headImage: null
    }
  },
  methods:{
    snapHandle(){   //截取图像
      this.context.drawImage(this.video, 0, 0, 200, 150);
      console.log(document.querySelector('canvas').toDataURL('image/png'));
    },
    closeHandle(){  //关闭摄像头
      this.mediaStreamTrack && this.mediaStreamTrack.stop();
    },
    photoHandle(){   //打开摄像头
      var that = this;
      navigator.getMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMeddia || navigator.msGetUserMedia;
      var canvas = document.querySelector('canvas');
      that.context = canvas.getContext('2d');
      that.video = document.querySelector('video');
      if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
        navigator.mediaDevices.getUserMedia({
          video: true,
          audio: true
        }).then(function(stream) {
          that.mediaStreamTrack = typeof stream.stop === 'function' ? stream : stream.getTracks()[1];
          that.video.src = (window.URL || window.webkitURL).createObjectURL(stream);
          that.video.play();
        }).catch(function(err) {
          console.log(err);
        })
      }else if (navigator.getMedia) {
        navigator.getMedia({
          video: true
        }, function(stream) {
          that.mediaStreamTrack = stream.getTracks()[0];
          that.video.src = (window.URL || window.webkitURL).createObjectURL(stream);
          that.video.play();
        }, function(err) {
          console.log(err);
        });
      }
    }
  }
}
</script>
<style scoped>
  canvas,video{
    margin: 0 auto 10px;
    border: 1px solid #DDD;
  }
  .btns{
    display: flex;
    justify-content: center;
  }
  .btns>div{
    width: 25%;
    height: 3rem;
    line-height: 3rem;
    margin: 20px 10px;
    background-color: #42B983;
    color: #FFF;
    font-weight: 800;
    font-size: 1.2rem;
    text-align: center;
  }
  .btns>div:active{
    background-color: #2D7D59;
  }
  .photo{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
</style>



