import Vue from 'vue'
import Router from 'vue-router'
import camera from '@/components/camera'
import geography from '@/components/geography'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'camera',
      component: camera
    },
    {
      path: '/geography',
      name: 'geography',
      component: geography
    }
  ]
})



