 <template>
 	<footer class="footer">
		<router-link :class="{active: index==='0'}" to='/'>拍照</router-link>
		<router-link :class="{active: index==='1'}" to='/geography'>经纬度</router-link>
 	</footer>
 </template>

 <script>
 export default {
   name: 'foot',
   props: ['index'],
 }
 </script>
 <style type="text/css" scoped>
	 .footer{
     position: fixed;
     left: 0;
     right: 0;
     bottom: 0;
     height: 4rem;
     line-height: 4rem;
     font-size: 1.3rem;
     display: flex;
     align-items: stretch;
     color: #333;
     background-color: #EFEFEF;
	 }
   .footer a{
     color: inherit;
     width: 0;
     flex-grow: 1;
     text-align: center;
     text-decoration: none;
     -webkit-tap-highlight-color: rgba(0,0,0,0)
   }
   .footer a:active{
     color: inherit;
   }
   .footer .active{
     color: red;
   }
 </style>


